@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:ital,wght@1,700&display=swap);
.purple-btn-outline{border:1px solid #7952b3 !important}.btn-outline{background-color:transparent !important;color:inherit !important;transition:all 0.5s}.btn-outline a::hover{color:inherit !important}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.list-item{list-style:none}.link{color:black;text-decoration:none}.link:hover{color:black;text-decoration:none !important}.link:active{color:black}.link:visited{color:black}.logo-font{font-family:"Noto Serif", serif}.purple-bg{background-color:#7952b3 !important}.purple{color:#7952b3 !important}.app-bg{background-color:aliceblue !important}

.App{text-align:center;height:100vh}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

